import { gql } from "@apollo/client"

export const ALL_CONTACT_BOOKS = gql`
    query allContactBooks ($filter: String = ""){
        allContactBooks (filter: $filter) {
            id
            student {
                name,
                familyName
            }
            teacher {
                name,
                familyName
            }
            startAt
            endAt
            createdAt
            updatedAt
        }
    }
`

export const CONTACT_BOOK_BY_ID = gql`
    query contactBookById ($id: ID!) {
        contactBookById (id: $id) {
            id
            student {
                name,
                familyName
            }
            teacher {
                name,
                familyName
            }
            startAt
            endAt
            createdAt
            updatedAt
        }
    }
`

export const CREATE_CONTACT_BOOK = gql`
    mutation createContactBook (
        $endAt: Date!
        $section: ID!
        $startAt: Date!
    ) {
        createContactBook (
            section: $section,
            startAt: $startAt,
            endAt: $endAt,
        ) {
            contactBook
        }
    }
`

export const UPDATE_CONTACT_BOOK = gql`
    mutation updateContactBook (
        $artAttention: String!
        $artContent: String!
        $bookAttention: String!
        $bookContent: String!
        $danceAttention: String!
        $danceContent: String!
        $fairyTaleAttention: String!
        $fairyTaleContent: String!
        $fairyTaleCreation: String!
        $habitsAttention: String!
        $habitsContent: String!
        $id: ID!
        $languageAttention: String!
        $languageContent: String!
        $musicAttention: String!
        $musicContent: String!
        $physicalAttention: String!
        $physicalContent: String!
    ) {
        updateContactBook (
            artAttention: $artAttention 
            artContent: $artContent 
            bookAttention: $bookAttention 
            bookContent: $bookContent 
            danceAttention: $danceAttention 
            danceContent: $danceContent 
            fairyTaleAttention: $fairyTaleAttention 
            fairyTaleContent: $fairyTaleContent 
            fairyTaleCreation: $fairyTaleCreation 
            habitsAttention: $habitsAttention 
            habitsContent: $habitsContent 
            id: $id 
            languageAttention: $languageAttention 
            languageContent: $languageContent 
            musicAttention: $musicAttention 
            musicContent: $musicContent 
            physicalAttention: $physicalAttention 
            physicalContent: $physicalContent 
        ) {
            contactBook {
                id
            }
        }
    }
`

export const DELETE_CONTACT_BOOK = gql`
    mutation deleteContactBook ($id: ID!) {
        deleteContactBook (id: $id) {
            contactBook{
                sleepMorning
            }
        }
    }
`

export const ALL_TEACHER_CONTACT_BOOKS = gql`
    query allTeacherContactBooks ($contactBook: ID) {
        allTeacherContactBooks (contactBook: $contactBook) {
            id
            contactBook {
                id
            }
            behavior
            physicalCondition
            sleepMorning
            sleepAfternoon
            isSleep
            sleepTime
            food
            morningFoodEat
            isMorningFoodLeft
            afternoonFoodEat
            isAfternoonFoodLeft
            eveningFoodEat
            isEveningFoodLeft
            wordToSay
            bodyTemperature
            pooMorning
            pooMorningCount
            pooAfternoon
            pooAfternoonCount
            date
        }
    }
`

export const CREATE_TEACHER_CONTACT_BOOK = gql`
    mutation createTeacherContactBook (
        $afternoonFoodEat: String!
        $behavior: String!
        $bodyTemperature: String!
        $contactBook: ID!
        $eveningFoodEat: String!
        $food: String!
        $isAfternoonFoodLeft: Boolean!
        $isEveningFoodLeft: Boolean!
        $isMorningFoodLeft: Boolean!
        $isSleep: Boolean!
        $morningFoodEat: String!
        $physicalCondition: String!
        $pooAfternoon: String!
        $pooAfternoonCount: Int!
        $pooMorning: String!
        $pooMorningCount: Int!
        $sleepAfternoon: String!
        $sleepMorning: String!
        $sleepTime: Int!
        $wordToSay: String!
    ) {
        createTeacherContactBook (
            afternoonFoodEat: $afternoonFoodEat
            behavior: $behavior
            bodyTemperature: $bodyTemperature
            contactBook: $contactBook
            eveningFoodEat: $eveningFoodEat
            food: $food
            isAfternoonFoodLeft: $isAfternoonFoodLeft
            isEveningFoodLeft: $isEveningFoodLeft
            isMorningFoodLeft: $isMorningFoodLeft
            isSleep: $isSleep
            morningFoodEat: $morningFoodEat
            physicalCondition: $physicalCondition
            pooAfternoon: $pooAfternoon
            pooAfternoonCount: $pooAfternoonCount
            pooMorning: $pooMorning
            pooMorningCount: $pooMorningCount
            sleepAfternoon: $sleepAfternoon
            sleepMorning: $sleepMorning
            sleepTime: $sleepTime
            wordToSay: $wordToSay
        ) {
            teacherContactBook {
                id
            }
        }
    }
`

