import React from 'react'
import { useMutation, useQuery } from '@apollo/client'
import { ALL_STUDENT_CONTACT_BOOKS, UPDATE_STUDENT_CONTACT_BOOK } from 'graphql/contact-book'
import { EditTwoTone, CloseSquareOutlined } from '@ant-design/icons';
import { Button, Col, Form, Input, Row, Upload, Spin, Switch, Tabs, InputNumber, Select } from 'antd'
import { ImageSvg } from 'assets/svg/icon';
import Loading from 'components/shared-components/Loading'
import IntlMessage from 'components/util-components/IntlMessage';
import { useState } from 'react';
import moment from 'moment';
import { useEffect } from 'react';
import { CheckPer } from 'hooks/checkPermission';
import { BASE_SERVER_URL } from 'configs/AppConfig';
import CustomIcon from 'components/util-components/CustomIcon';
import { convertBehaviorStatus } from 'utils';

const { Dragger } = Upload;
const { TabPane } = Tabs;
const { Option } = Select;

function ContactForm({ loading, onFinish, initialValues }) {

    const [form] = Form.useForm()
    const [selectedImage, setSelectedImage] = useState();

    const imageProps = {
        accept: '.jpg',
        name: 'file',
        multiple: false,
        listType: "picture-card",
        showUploadList: false,
        beforeUpload: file => {
            return false
        }
    };

    const onImageChange = (file) => {
        if (file) {
            setSelectedImage(file.file)
        }
    }

    const normFile = (e) => {
        if (Array.isArray(e)) {
            return e;
        }
        return e && e.fileList;
    };

    useEffect(() => {
        if (initialValues) {
            form.setFieldsValue({
                physicalCondition: initialValues.physicalCondition,
                sleep: initialValues.sleep,
                morningFoodEat: initialValues.morningFoodEat,
                defecate: initialValues.defecate,
                wordToSay: initialValues.wordToSay,
                attandance: initialValues.attandance
            })
        }
    }, [initialValues, form])

    return (
        <Spin spinning={loading} tip="Ачааллаж байна...">
            <Form
                id="ContactBookForm"
                layout={'vertical'}
                form={form}
                name="school"
                onFinish={onFinish}
            >
                <Row gutter={[16, 24]}>
                    <Col className="gutter-row" span={12}>
                        <Form.Item name="sleep" label={<IntlMessage id="sleep" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Select>
                                <Option value="GOOD" >Сайн</Option>
                                <Option value="MEDIUM" >Дунд</Option>
                                <Option value="BAD" >Муу</Option>
                            </Select>
                        </Form.Item>
                        <Form.Item name="physicalCondition" label={<IntlMessage id="physicalCondition" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Input.TextArea />
                        </Form.Item>
                        <Form.Item name="attandance" label={<IntlMessage id="attandance" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Select>
                                <Option value="ARRIVED" >Ирсэн</Option>
                                <Option value="FREE" >Чөлөөтэй</Option>
                                <Option value="SICK" >Өвчтэй</Option>
                            </Select>
                        </Form.Item>
                        <Form.Item
                            name="file"
                            label={<IntlMessage id="file" />}
                            valuePropName='fileList'
                            getValueFromEvent={normFile}
                        >
                            <Dragger
                                {...imageProps}
                                onChange={e => onImageChange(e)}
                            >
                                {selectedImage ?
                                    <img src={URL.createObjectURL(selectedImage)} alt="avatar" className="img-fluid max-h-40" />
                                    :
                                    <div>
                                        {initialValues.file ?
                                            <img src={BASE_SERVER_URL + initialValues.file} alt="avatar" className="img-fluid max-h-40" />
                                            :
                                            <div>
                                                <CustomIcon className="display-3" svg={ImageSvg} />
                                                <p>Файлыг байршуулахын тулд товшиж эсвэл чирнэ үү</p>
                                            </div>
                                        }
                                    </div>
                                }
                            </Dragger>
                        </Form.Item>
                    </Col>
                    <Col className="gutter-row" span={12}>
                        <Form.Item name="morningFoodEat" label={<IntlMessage id="morningFoodEat" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Select>
                                <Option value="GOOD" >Сайн</Option>
                                <Option value="MEDIUM" >Дунд</Option>
                                <Option value="BAD" >Муу</Option>
                            </Select>
                        </Form.Item>
                        <Form.Item name="wordToSay" label={<IntlMessage id="wordToSay" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Input.TextArea />
                        </Form.Item>
                        <Form.Item name="defecate" label={<IntlMessage id="defecate" />} rules={[
                            {
                                required: true,
                                message: <IntlMessage id="form.required" />
                            },
                        ]}>
                            <Input />
                        </Form.Item>
                    </Col>
                </Row>
                <Form.Item className='text-right'>
                    <Button type="primary" htmlType="submit">
                        Хадгалах
                    </Button>
                </Form.Item>
            </Form>
        </Spin>
    )
}

function SingleContactBook({ data, contactBook }) {

    const [isEdit, setIsEdit] = useState(false)

    const canEdit = CheckPer('change_studentcontactbook')

    const [updateContact, { loading }] = useMutation(UPDATE_STUDENT_CONTACT_BOOK, {
        onCompleted: res => {
            setIsEdit(false)
        },
        refetchQueries: [{
            query: ALL_STUDENT_CONTACT_BOOKS,
            variables: { contactBook: contactBook }
        }],
    })

    function toggleEdit() {
        setIsEdit(prevState => !prevState)
    }

    function onFinish(values) {
        values.id = data.id
        if (values.file) {
            const lastIndexOfFiles = values.file.pop()
            values.file = lastIndexOfFiles.originFileObj
        } else {
            values.file = ""
        }
        updateContact({ variables: values })
    }

    return (
        <div className=''>
            <div className="flex justify-between px-4 sm:px-0">
                <h3 className="text-base font-semibold leading-7 text-gray-900">{moment(data.date).format("YYYY-MM-DD")} өдрийн тэмдэглэл</h3>
                {canEdit &&
                    <div>
                        {isEdit ?
                            <Button type="text" onClick={toggleEdit} icon={<CloseSquareOutlined />} > <IntlMessage id="main.close" /> </Button>
                            :
                            <Button type="text" onClick={toggleEdit} icon={<EditTwoTone twoToneColor='#ffdb00' />} > <IntlMessage id="edit" /> </Button>}
                    </div>
                }
            </div>
            {isEdit ?
                <ContactForm
                    onFinish={onFinish}
                    loading={loading}
                    initialValues={data}
                />
                :
                <div className="mt-6 border-t border-gray-100">
                    <dl className="divide-y divide-gray-100">
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="attandance" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                                {data.attandance === 'ARRIVED' ? 'Ирсэн' : 
                                    data.attandance === 'FREE' ? 'Чөлөөтэй' :
                                    'Өвчтэй'
                                }
                            </dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="morningFoodEat" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                                {convertBehaviorStatus(data.morningFoodEat)}
                            </dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="sleep" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                                {convertBehaviorStatus(data.sleep)}
                            </dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="defecate" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.defecate}</dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="physicalCondition" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">{data.physicalCondition}</dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="wordToSay" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                                {data.wordToSay}
                            </dd>
                        </div>
                        <div className="px-4 py-2 sm:grid sm:grid-cols-3 sm:gap-4 sm:px-0">
                            <dt className="text-sm font-medium leading-6 text-gray-900"><IntlMessage id="main.image" /></dt>
                            <dd className="mt-1 text-sm leading-6 text-gray-700 sm:col-span-2 sm:mt-0">
                                <img src={BASE_SERVER_URL + data.file} alt="file" className="rounded-2 max-h-40" />
                            </dd>
                        </div>
                    </dl>
                </div>
            }
        </div>
    )
}

export default function StudentContactBook({ contactBook }) {

    const [selected, setSelected] = useState({})

    const { data, loading } = useQuery(ALL_STUDENT_CONTACT_BOOKS, {
        variables: { contactBook: contactBook },
        onCompleted: res => {
            setSelected(res.allStudentContactBooks[0])
        }
    })

    if (loading) return <Loading cover='content' />

    return (
        <div className='bg-white p-4 rounded-4 w-full'>
            <Tabs
                tabPosition='top'
                defaultActiveKey={0}
                size='small'
                onChange={e => setSelected(data.allStudentContactBooks[e])}
            >
                {data.allStudentContactBooks.map((item, index) => (
                    <TabPane
                        key={index}
                        tab={
                            <span>
                                {moment(item.date).format("YYYY-MM-DD")}
                            </span>
                        }
                    >
                        <SingleContactBook
                            data={selected}
                            contactBook={contactBook}
                        />
                    </TabPane>
                ))}
            </Tabs>

        </div>
    )
}
