import React from 'react'
import { useQuery } from '@apollo/client';
import Loading from 'components/shared-components/Loading';
import IntlMessage from 'components/util-components/IntlMessage';
import { CONTACT_BOOK_BY_ID } from 'graphql/contact-book';
import moment from 'moment';
import { useParams, Link } from 'react-router-dom'
import { Button } from 'antd';
import { RollbackOutlined } from '@ant-design/icons';
import StudentContactBook from './student-contact-book';

export default function Index() {
    
    const { contactBook } = useParams()

    const { data, loading } = useQuery(CONTACT_BOOK_BY_ID, {
        variables: { id: contactBook }
    })

    if (loading) return <Loading cover='content' />

    return (
        <>
            <div>
                <Link to={'/app/contact-book'}>
                    <Button type="text" icon={<RollbackOutlined />} > <IntlMessage id="back" /> </Button>
                </Link>
            </div>
            <div className='bg-white p-4 rounded-4 mt-4'>
                <div className=''>
                    <h3 className="text-center text-2xl font-semibold leading-7 text-gray-900">
                        {moment(data.contactBookById.startAt).format('YYYY-MM-DD')} / {moment(data.contactBookById.endAt).format('YYYY-MM-DD')} өдрийн тэмдэглэл
                    </h3>
                    <StudentContactBook contactBook={contactBook} />
                </div>
            </div>
        </>

    )
}
